@import url(https://fonts.googleapis.com/css2?family=Cabin:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
body {
  margin: 0;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family:
    source-code-pro,
    Menlo,
    Monaco,
    Consolas,
    'Courier New',
    monospace;
}

.calculator-section {
  width: 80%;
  margin: auto;
  padding: 10% 2%;
  display: flex;
  flex-direction: row;
  color: #03071e;
  justify-content: space-around;
}

.calculator {
  width: 35%;
}

.screen {
  background-color: #858694;
  text-align: right;
  margin-bottom: 0;
  height: 3rem;
  color: white;
  font-size: 20px;
  padding: 15px 5px 0 0;
}

.buttons {
  display: flex;
  flex-direction: row-reverse;
  height: 10rem;
}

.vertical {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 15%;
}

.cornerButton {
  height: 20%;
  background-color: #f48c06;
  border-width: 1px;
}

.inner-container {
  display: grid;
  grid-template-columns: auto auto auto;
  width: 85%;
  height: 100%;
}

.innerButton {
  background-color: #e9d8a6;
  border-width: 1px;
}

#zero {
  grid-column: 1 / 3;
}

.navbar {
  display: flex;
  justify-content: space-around;
  align-content: center;
  background-color: #370617;
  color: white;
}

a {
  text-decoration: none;
  color: white;
  font-family: "Cabin", sans-serif;
}

.nav-link:hover {
  font-size: large;
}

.active-link {
  color: #faa307;
  font-weight: bold;
}

.navbar span {
  width: 40%;
  align-self: center;
  font-family: "Cabin", sans-serif;
  font-size: 30px;
}

.navbar ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  width: 30%;
}

section {
  font-family: "Oxygen", sans-serif;
}

section {
  width: 95%;
  margin: auto;
  padding: 10% 5%;
  display: flex;
  flex-direction: column;
  color: #03071e;
}

.author {
  align-self: center;
  font-weight: bold;
}

.quote {
  font-family: "Dancing Script", cursive;
}

